:root {
  /* Família de Fontes (garanta que estejam carregadas via layout.tsx ou aqui) */
  --font-family-sans: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --font-family-mono: 'Geist Mono', ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace; /* Adicionando fallbacks comuns */
  --auth-card-max-width: 28rem; /* ou 448px */  
  
  /* Paleta de Cores Inspirada na Imagem de Referência (Tema Claro como padrão) */
  --color-primary: #4A90E2;       /* Azul principal da imagem de referência */
  --color-primary-hover: #357ABD; /* Um tom mais escuro para hover */
  --color-primary-rgb: 74, 144, 226; /* Para usar com opacidade (ex: box-shadow) */

  --color-text-base: #2D3748;        /* Cinza escuro para texto principal (Tailwind gray-800) */
  --color-text-muted: #718096;       /* Cinza médio para texto secundário (Tailwind gray-600) */
  --color-text-on-primary: #FFFFFF;  /* Texto branco sobre fundos com cor primária */

  --color-background-page: #F7FAFC;    /* Fundo da página (Tailwind gray-100) */
  --color-background-element: #FFFFFF; /* Fundo de cards, sidebar, navbar (Branco) */

  --color-border: #E2E8F0;           /* Borda sutil (Tailwind gray-300) */
  --color-border-input: #CBD5E0;     /* Borda para inputs (Tailwind gray-400) */

  /* Cores de Feedback */
  --color-success: #38A169; /* Verde (Tailwind green-600) */
  --color-error: #E53E3E;   /* Vermelho (Tailwind red-600) */
  --color-warning: #DD6B20; /* Laranja (Tailwind orange-600) */

  /* Espaçamentos (consistentes com Tailwind ou customizados) */
  --spacing-xs: 0.25rem; /* 4px */
  --spacing-sm: 0.5rem;  /* 8px */
  --spacing-md: 1rem;    /* 16px */
  --spacing-lg: 1.5rem;  /* 24px */
  --spacing-xl: 2rem;    /* 32px */
  --spacing-2xl: 2.5rem; /* 40px */

  /* Bordas Arredondadas */
  --border-radius-sm: 0.25rem;  /* 4px */
  --border-radius-md: 0.375rem; /* 6px - rounded-md */
  --border-radius-lg: 0.5rem;   /* 8px - rounded-lg */
  --border-radius-xl: 0.75rem;  /* 12px - rounded-xl */

  /* Transições */
  --transition-fast: 0.15s ease-in-out;
  --transition-std: 0.2s ease-in-out;
  --transition-slow: 0.3s ease-in-out;

  /* Sombras */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.07), 0 2px 4px -2px rgba(0, 0, 0, 0.07); /* Sombra mais suave */
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.07), 0 4px 6px -4px rgba(0, 0, 0, 0.07);
  --shadow-focus-ring: 0 0 0 3px rgba(var(--color-primary-rgb), 0.4);

  /* Variáveis genéricas para Tailwind, para compatibilidade com ThemeProvider */
  --background: var(--color-background-page);
  --foreground: var(--color-text-base);
  --card-background: var(--color-background-element);
  --border-color: var(--color-border);
  --input-background: var(--color-background-element);

  /* Variáveis para botões (usadas pelas classes .btn-*) */
  --btn-primary-bg: var(--color-primary);
  --btn-primary-text: var(--color-text-on-primary);
  --btn-primary-hover-bg: var(--color-primary-hover);

  --btn-secondary-bg: var(--color-background-element);
  --btn-secondary-text: var(--color-primary);
  --btn-secondary-border: var(--color-primary);
  --btn-secondary-hover-bg: rgba(var(--color-primary-rgb), 0.08);
  --btn-secondary-hover-border: var(--color-primary-hover);
  --btn-secondary-hover-text: var(--color-primary-hover);
}

body.dark-mode {
  --color-primary: #60A5FA;        /* Azul Tailwind 400 */
  --color-primary-hover: #3B82F6;  /* Azul Tailwind 500 */
  --color-primary-rgb: 96, 165, 250;

  --color-text-base: #E5E7EB;         /* Cinza Tailwind 200 */
  --color-text-muted: #9CA3AF;        /* Cinza Tailwind 400 */
  --color-text-on-primary: #1A202C;   /* Cinza Tailwind 900 (para contraste com azul claro) */

  --color-background-page: #111827;     /* Cinza Tailwind 900 */
  --color-background-element: #1F2937;  /* Cinza Tailwind 800 */

  --color-border: #374151;            /* Cinza Tailwind 700 */
  --color-border-input: #4B5563;      /* Cinza Tailwind 600 */

  --color-success: #34D399; /* Verde Esmeralda 400 */
  --color-error: #F87171;   /* Vermelho 400 */
  --color-warning: #FBBF24; /* Âmbar 400 */

  /* Atualiza variáveis genéricas para modo escuro */
  --background: var(--color-background-page);
  --foreground: var(--color-text-base);
  --card-background: var(--color-background-element);
  --border-color: var(--color-border);
  --input-background: var(--color-background-element); /* Ou um tom ligeiramente diferente, ex: #2D3748 (gray-700) */

  /* Botões no modo escuro */
  --btn-primary-bg: var(--color-primary);
  --btn-primary-text: var(--color-text-on-primary);
  --btn-primary-hover-bg: var(--color-primary-hover);

  --btn-secondary-bg: var(--color-background-element);
  --btn-secondary-text: var(--color-primary);
  --btn-secondary-border: var(--color-primary);
  --btn-secondary-hover-bg: rgba(var(--color-primary-rgb), 0.15);
  --btn-secondary-hover-border: var(--color-primary-hover);
  --btn-secondary-hover-text: var(--color-primary-hover);
}

/* ========================================= */
/* Estilos Globais Base                      */
/* ========================================= */
body {
  margin: 0;
  font-family: var(--font-family-sans);
  background-color: var(--background); /* Usa variável genérica */
  color: var(--foreground);       /* Usa variável genérica */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--foreground);
  margin-top: 0;
  margin-bottom: var(--spacing-md);
  font-weight: 600;
  line-height: 1.3;
}

h1 { font-size: 2rem; font-weight: 700; }
h2 { font-size: 1.5rem; font-weight: 700; }
h3 { font-size: 1.25rem; font-weight: 600; } /* Título da página/seção */
h4 { font-size: 1.125rem; font-weight: 600; } /* Título de card */

p {
  margin-top: 0;
  margin-bottom: var(--spacing-md);
  color: var(--color-text-muted); /* Parágrafos com cor suave por padrão */
}
strong {
  font-weight: 600;
  color: var(--foreground); /* Destaque para strong */
}

a:not(.btn) { /* Links que não são botões */
  color: var(--color-primary);
  text-decoration: none;
  transition: color var(--transition-fast);
}
a:not(.btn):hover {
  color: var(--color-primary-hover);
  text-decoration: underline;
}

/* ========================================= */
/* Estilos para Componentes Comuns           */
/* ========================================= */

/* Botões */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-sm) var(--spacing-md);
  font-size: 0.875rem;
  font-weight: 500;
  border-radius: var(--border-radius-md);
  border: 1px solid transparent;
  cursor: pointer;
  transition: background-color var(--transition-std), border-color var(--transition-std), color var(--transition-std), box-shadow var(--transition-std), transform var(--transition-fast);
  text-decoration: none;
  line-height: 1.5;
  white-space: nowrap; /* Evita quebra de linha em botões */
}
.btn:focus-visible {
  outline: 2px solid transparent;
  outline-offset: 2px;
  box-shadow: var(--shadow-focus-ring);
}
.btn:hover {
  transform: translateY(-1px); /* Leve elevação no hover */
}
.btn:active {
  transform: translateY(0);
}

.btn-primary {
  background-color: var(--btn-primary-bg);
  color: var(--btn-primary-text);
  box-shadow: var(--shadow-sm);
}
.btn-primary:hover {
  background-color: var(--btn-primary-hover-bg);
  box-shadow: var(--shadow-md);
}

.btn-secondary, .btn-outline {
  background-color: var(--btn-secondary-bg);
  color: var(--btn-secondary-text);
  border-color: var(--btn-secondary-border);
  box-shadow: var(--shadow-sm);
}
.btn-secondary:hover, .btn-outline:hover {
  background-color: var(--btn-secondary-hover-bg);
  border-color: var(--btn-secondary-hover-border);
  color: var(--btn-secondary-hover-text);
  box-shadow: var(--shadow-md);
}

.btn-danger {
  background-color: var(--color-error);
  color: #FFFFFF;
  box-shadow: var(--shadow-sm);
}
.btn-danger:hover {
  background-color: #c53030; /* Vermelho Tailwind 700 */
  box-shadow: var(--shadow-md);
}

.btn-reset { /* Um estilo mais sutil para reset/limpar */
  background-color: transparent;
  color: var(--color-text-muted);
  border-color: transparent;
}
.btn-reset:hover {
  color: var(--foreground);
  background-color: rgba(var(--gray-rgb, 107, 114, 128), 0.1); /* Use uma var gray-rgb */
}


.btn-sm {
  padding: var(--spacing-xs) var(--spacing-sm);
  font-size: 0.75rem;
}

/* Formulários */
.form-group {
  margin-bottom: var(--spacing-lg); /* Mais espaço entre grupos de formulário */
}

.form-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--foreground);
  margin-bottom: var(--spacing-xs);
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
select,
textarea {
  width: 100%;
  padding: calc(var(--spacing-sm) + 2px) var(--spacing-md); /* Ajuste para altura consistente */
  font-size: 0.875rem;
  line-height: 1.5;
  color: var(--foreground);
  background-color: var(--input-background);
  border: 1px solid var(--color-border-input);
  border-radius: var(--border-radius-md);
  transition: border-color var(--transition-std), box-shadow var(--transition-std);
  box-shadow: var(--shadow-sm);
}
input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="number"]:focus,
select:focus,
textarea:focus {
  border-color: var(--color-primary);
  outline: none;
  box-shadow: var(--shadow-focus-ring);
}

select {
  appearance: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%23718096' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e"); /* Cor do ícone (gray-500) */
  background-position: right var(--spacing-md) center;
  background-repeat: no-repeat;
  background-size: 1em 1em;
  padding-right: calc(var(--spacing-md) * 2.5 + 0.5rem);
}
body.dark-mode select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%239CA3AF' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e"); /* Cor do ícone (gray-400) */
}


textarea {
  min-height: 120px;
}

input[type="checkbox"] {
  width: 1rem; height: 1rem;
  border-radius: var(--border-radius-sm);
  border: 1px solid var(--color-border-input);
  color: var(--color-primary); /* Cor do checkmark */
  margin-right: var(--spacing-sm);
  box-shadow: none;
  vertical-align: middle;
}
input[type="checkbox"]:focus {
  border-color: var(--color-primary);
  box-shadow: var(--shadow-focus-ring);
}
.checkbox-label-group {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
}
.checkbox-label-group label {
  margin-bottom: 0;
  font-weight: normal;
}


/* Estilos para as páginas .Create que usam tabela para layout de formulário */
.Create { /* Container da página de criação/edição */
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}
.Create h3 { /* Título da página de criação/edição */
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: var(--spacing-lg);
}
.Create form {
  background-color: var(--card-background);
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-color);
}
.Create form table {
  border-collapse: separate;
  border-spacing: 0 var(--spacing-lg); /* Aumenta o espaço entre as linhas do form */
  width: 100%;
  margin-top: 0;
  box-shadow: none;
  border: none;
}
.Create form td {
  padding: 0; /* Padding será nos elementos internos */
  border: none;
  text-align: left;
  vertical-align: top; /* Para labels e inputs multiline */
}
.Create form td:first-child { /* Label cell */
  padding-right: var(--spacing-lg);
  width: 150px; /* Largura fixa para labels, ajuste conforme necessário */
  padding-top: calc(var(--spacing-sm) / 2 + 2px); /* Alinhamento vertical com input */
}
.Create form label {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--foreground);
  display: block; /* Garante que o label ocupe a largura */
}
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-md);
  padding-top: var(--spacing-lg);
  margin-top: var(--spacing-lg);
  border-top: 1px solid var(--border-color);
}

/* Tabelas de Listagem */
table:not(.Create form table) {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin-top: var(--spacing-lg);
  background-color: var(--card-background);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
  font-size: 0.875rem;
  border: 1px solid var(--border-color);
  overflow: hidden;
}

table:not(.Create form table) thead {
  background-color: var(--table-header-bg, #F9FAFB); /* var para tema escuro */
}
body.dark-mode table:not(.Create form table) thead {
  --table-header-bg: #2D3748; /* gray-800 */
}

table:not(.Create form table) th {
  padding: var(--spacing-md) var(--spacing-lg);
  text-align: left;
  font-weight: 600;
  color: var(--color-text-muted);
  border-bottom: 1px solid var(--border-color); /* Borda sutil no cabeçalho */
}

table:not(.Create form table) td {
  padding: var(--spacing-md) var(--spacing-lg);
  border-bottom: 1px solid var(--border-color);
  color: var(--foreground);
  vertical-align: middle;
}

table:not(.Create form table) tbody tr:last-child td {
  border-bottom: none;
}

table:not(.Create form table) tbody tr:hover {
  background-color: rgba(var(--color-primary-rgb), 0.05);
}
table:not(.Create form table) td .btn { /* Botões dentro de células de tabela */
  margin-right: var(--spacing-xs);
}
table:not(.Create form table) td .btn:last-child {
  margin-right: 0;
}


/* Containers de Página */
.page-container {
  padding: var(--spacing-lg) var(--spacing-xl); /* Espaçamento generoso */
}
.table-header-actions { /* Cabeçalho de páginas de listagem */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-lg);
}
.table-header-actions h3 {
  margin-bottom: 0;
  font-size: 1.75rem;
  font-weight: 700;
}

/* Responsividade de Tabela de Listagem (se for usar o método data-label) */
@media (max-width: 768px) {
  .page-container {
    padding: var(--spacing-md);
  }
  .table-header-actions {
    flex-direction: column;
    align-items: stretch; /* Título e botões em largura total */
    gap: var(--spacing-md);
  }
  .table-header-actions .button-group {
    display: flex;
    flex-direction: column; /* Botões empilhados ou ajustar para ficarem lado a lado */
    gap: var(--spacing-sm);
  }
  .table-header-actions .btn {
    width: 100%; /* Botões de ação em largura total no mobile */
  }

  table:not(.Create form table) {
    border: 0;
    box-shadow: none;
    background-color: transparent; /* Sem fundo para a "tabela" em si */
  }
  table:not(.Create form table) thead {
    display: none;
  }
  table:not(.Create form table) tr {
    display: block;
    margin-bottom: var(--spacing-md);
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius-lg);
    background-color: var(--card-background); /* Cada linha vira um card */
    box-shadow: var(--shadow-md);
    padding: var(--spacing-md);
  }
  table:not(.Create form table) td {
    display: block;
    text-align: right;
    padding: var(--spacing-sm) 0;
    padding-left: 45%;
    position: relative;
    border-bottom: 1px solid var(--border-color);
  }
  table:not(.Create form table) td:last-child {
    border-bottom: 0;
  }
  table:not(.Create form table) td::before {
    content: attr(data-label);
    position: absolute;
    left: 0;
    width: 40%;
    padding-right: var(--spacing-sm);
    font-weight: 500; /* Medium */
    text-align: left;
    color: var(--foreground);
  }
}

/* Estilos para a página de edição de questionário com modo de visualização/edição */
/* frontend/src/app/questionarios/[id]/page.tsx */
.editor-form-card {
  width: 100%;
  background-color: var(--card-background);
  border-radius: var(--border-radius-lg);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-md);
  overflow: hidden;
  margin-top: var(--spacing-lg);
}

.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-md) var(--spacing-lg);
  background-color: transparent;
  border-bottom: 1px solid var(--border-color);
}

.form-header h3 {
  margin: 0;
  font-size: 1.125rem; /* text-lg */
  font-weight: 600;
  color: var(--foreground);
}

.form-header-actions { display: flex; gap: var(--spacing-sm); }

.display-section {
  padding: var(--spacing-lg);
  border-bottom: 1px solid var(--border-color);
}
.display-section:last-child {
  border-bottom: none;
}

.display-section > label { /* Label da seção (Título, Perguntas) */
  display: block;
  font-size: 0.75rem; /* text-xs */
  font-weight: 600;
  text-transform: uppercase;
  color: var(--color-text-muted);
  margin-bottom: var(--spacing-sm);
  letter-spacing: 0.05em;
}

.input-edit-mode { /* Para inputs e textareas no modo de edição */
  width: 100%;
  border: 1px solid var(--color-border-input);
  border-radius: var(--border-radius-md);
  padding: var(--spacing-sm) var(--spacing-md);
  background-color: var(--input-background);
  color: var(--foreground);
  transition: border-color var(--transition-std), box-shadow var(--transition-std);
}
.input-edit-mode:focus {
  border-color: var(--color-primary);
  box-shadow: var(--shadow-focus-ring);
  outline: none;
}
.title-input { /* Input para o título do questionário */
  font-size: 1.5rem; /* text-2xl */
  font-weight: 700;
  padding: var(--spacing-md); /* Mais padding para título */
}
.question-textarea { /* Textarea para enunciado da pergunta */
  font-size: 1rem; /* text-base */
  resize: vertical;
  min-height: 60px;
}

.perguntas-edit-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg); /* Espaço entre cada item de edição de pergunta */
}

.pergunta-editor-item {
  background-color: rgba(var(--gray-rgb, 0,0,0), 0.02); /* Fundo sutil para cada pergunta */
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-md);
  padding: var(--spacing-md);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}
body.dark-mode .pergunta-editor-item {
  background-color: rgba(var(--gray-rgb-dark, 255,255,255), 0.03); /* Ajustar para tema escuro */
}

.pergunta-meta-editor {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}
.pergunta-meta-editor label, .opcoes-editor-container > label { /* Labels dentro do editor de pergunta */
  font-size: 0.75rem;
  font-weight: 500;
  color: var(--color-text-muted);
}

.opcoes-editor-container {
  border-top: 1px dashed var(--color-border);
  padding-top: var(--spacing-md);
  margin-top: var(--spacing-sm);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.opcao-editor-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
}
.opcao-editor-item input[type="text"] {
  flex-grow: 1; /* Input da opção ocupa o espaço */
}

.btn-remover-opcao, .btn-adicionar-opcao {
  border-radius: var(--border-radius-md);
  padding: var(--spacing-xs) var(--spacing-sm);
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color var(--transition-std), color var(--transition-std);
}
.btn-remover-opcao {
  background-color: transparent;
  border: none;
  color: var(--color-error);
  padding: calc(var(--spacing-sm) + 2px); /* Para alinhar com altura do input */
}
.btn-remover-opcao:hover {
  color: #c53030; /* Vermelho mais escuro */
}

.btn-adicionar-opcao {
  background-color: transparent;
  border: 1px dashed var(--color-primary);
  color: var(--color-primary);
  text-align: center;
  margin-top: var(--spacing-sm);
}
.btn-adicionar-opcao:hover {
  background-color: rgba(var(--color-primary-rgb), 0.1);
  color: var(--color-primary-hover);
  border-style: solid;
}
